$main: #5c0431;

.CourseCard{
    margin-top: 5rem;
    border-radius: 0;
    width: 30%;
    border: none;
    
    img{
        width: 80%;
        margin-left: 10%;
        height: auto;
    }
    .card-body{
        overflow-x: hidden!important;
        h5{
            font-size: 2.5rem;
            font-family: 'Quicksand', sans-serif;
        }
        p{
            font-family: 'Quicksand', sans-serif;
            
        }
        .stars{
            text-align: left!important;
            transform: translateX(-100%);
        }
        .prices{
            text-align: right!important;
            transform: translateX(200%);
            h6{
                font-size: 1rem;
                font-family: 'Quicksand', sans-serif;
            }
        }
        .students{
            text-align: left;
            transform: translate(-100%);
            h4{
                font-family: 'Quicksand', sans-serif;
                font-size: 1rem;
            }
        }
        .btn{
            position: relative;
            top: -5rem;
            background: none;
            margin-top: 2rem;
            margin-bottom: 1rem;
            font-size: 1.4rem;
            font-family: 'Quicksand', sans-serif;
            border: $main 1px solid;
            color: $main;
        }
        .btn:hover{
            background: $main;
            color: white;
            border-radius: 25px;
            box-shadow: 0px 0px 60px $main;
        }
    }
}
.CourseCard:hover{
    border-bottom: $main 2px solid;
    transition: all .3s;
    transform: scale(1.03);
    .stars{
        transition: all .4s;
        transform: translateX(0%);
    }
    .prices{
        transition: all .4s;
        transform: translateX(0%);
    }
    .students{
        transition: all .4s;
        transform: translate(0%);
    }
    .btn{
        transition: all .4s;
        top: 0;
    }
    .card-body{
        h5{
            transition: all .5s;
            color: $main;
            text-shadow: 1px 1px 10px $main;
        }
    }
}






